:root {
  --accent-color: #d41443;
  --second-accent-color: #ffa5ba;
  --white-color: #fff;
  --white-background-color: #fff;
  --milk-background-color: #fff5f6;
  --pink-background-color: #ffb8ca;
  --brown-background-color: #f0d1a5;
  --green-background-color: #c2e297;
  --modal-background-color: #e17992;
  --review-background-color: #fafafa;
  --linear-gradient-color: #e1e1e1;
  --grey-background-button: #ecebeb;
  --white-text-color: #fff;
  --black-text-color: #000;
  --grey-text-color: #907e82;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

.visually__hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.up {
  width: 70px;
  height: 70px;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  z-index: 999;
  background: url("woman-1.90cfaf26.png") 0 0 / cover;
  transition: all .5s;
  position: fixed;
  bottom: 40px;
  right: 40px;
}

.up.active {
  visibility: visible;
  opacity: 1;
}

