.advantages {
  width: 100%;
  padding-bottom: 60px;
  background-color: var(--milk-background-color);

  @include desktop {
    padding-bottom: 120px;
  }
}

.advantages__container {
  @include tablet {
    padding-left: 23px;
  }

  @include desktop {
    padding-left: 16px;
  }
}

.advantages__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
  @include tablet {
    flex-direction: inherit;
    gap: 4px;
  }

  @include desktop {
    gap: 115px;
  }
}

.advantages__item-milk {
  &::before {
    content: '';
    display: block;
    width: 33px;
    height: 50px;
    background-image: url('../images/advantages1-mob.png');
    background-size: contain;
    background-repeat: no-repeat;
    margin-bottom: 16px;

    @include retina {
      background-image: url('../images/advantages1-mob-2x.png');
    }

    @include tablet {
      width: 50px;
      margin-bottom: 36px;
      background-image: url('../images/advantages1-tab.png');

      @include retina {
        background-image: url('../images/advantages1-tab-2x.png');
      }
    }

    @include desktop {
      margin-bottom: 40px;
      background-image: url('../images/advantages1-desc.png');

      @include retina {
        background-image: url('../images/advantages1-desc-2x.png');
      }
    }
  }
}

