.advantages__top-text {
  font-family: 'Titan One', cursive;
  @include font(400, 36px, 1.13, 0.04em);

  color: var(--accent-color);
  margin-bottom: 4px;

  @include tablet {
    @include font(null, 48px, 1.14, null);
    margin-bottom: 13px;
  }

  @include desktop {
    @include font(null, 58px, 1.13, null);
    margin-bottom: 16px;
  }
}

.advantages__text {
  max-width: 230px;
  @include font(700, 12px, 1.66, null);
  color: var(--black-text-color);

  @include tablet {
    max-width: 180px;
    @include font(null, 14px, 1.71, null);
  }

  @include desktop {
    max-width: 288px;
    @include font(null, 16px, 1.75, 0.02em);
  }
}
